<template>
   <div class="footer">
       <div class="footer-left">
           <div class="footer-shop-wrapper">
               <div class="iconfont shop">&#xe600;</div>
               <div class="shop-text">店铺</div>
           </div>
           <div class="footer-service-wrapper">
               <div class="iconfont service">&#xe624;</div>
               <div class="service-text">客服</div>
           </div>
           <div class="footer-star-wrapper">
               <div class="iconfont star">&#xe646;</div>
               <div class="star-text">收藏</div>
           </div>
       </div>
       <el-row class="footer-right">
           <el-button round class="footer-addCart">加入购物车</el-button>
           <el-button round class="footer-buyNow">立即购买</el-button>
       </el-row>
  </div>
</template>

<script>
export default {
  name: 'Footer'
}
</script>

<style scoped lang="less">
@import '../../assets/styles/varibles.less';
@import '../../assets/styles/mixins.less';
.footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: .9rem;
    background-color: #F5F5F5;
    .footer-left {
        float: left;
        width: 40%;
        height: .8rem;
        margin: .05rem 0;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        // font-size: .3rem;
        // background-color: hotpink;
        .footer-shop-wrapper {
            .shop {
                height: .45rem;
                font-size: .45rem;
            }
            .shop-text {
                height: .35rem;
                line-height: .35rem;
                font-size: .25rem;
                font-weight: lighter;
            }
        }
        .footer-service-wrapper {
            .service {
                height: .45rem;
                line-height: .45rem;
                font-size: .4rem;
            }
            .service-text {
                height: .35rem;
                line-height: .35rem;
                font-size: .25rem;
                font-weight: lighter;
            }
        }
        .footer-star-wrapper {
            .star {
                height: .45rem;
                font-size: .45rem;
            }
            .star-text {
                height: .35rem;
                line-height: .35rem;
                font-size: .25rem;
                font-weight: lighter;
            }
        }
    }
    .footer-right {
        float: right;
        width: 58%;
        // height: .7rem;
        // margin: .1rem .1rem .1rem 0;
        font-size: .3rem;
        .footer-addCart {
            float: left;
            width: 2.3rem;
            height: .74rem;
            margin: .08rem 0;
            color: whitesmoke;
            background-color: orange;
        }
        .footer-buyNow {
            float: left;
            width: 2rem;
            height: .74rem;
            margin: .08rem 0 .08rem .26rem;
            color: whitesmoke;
            background-color: orangered;
        }
    }
}
</style>



