<template>
   <div class="header">
    <div class="icon-down" v-show="showIconDown" :style="downIconStyle">
       <div class="icon-down-back-wrapper">
          <div class="iconfont back">&#xe604;</div>
        </div>
        <div class="icon-down-forward-wrapper">
          <div class="iconfont forward">&#xe60b;</div>
        </div>
        <div class="icon-down-more-wrapper">
          <div class="iconfont more">&#xe62b;</div>
        </div>
    </div>
    <div class="header-menu" v-show="showHeaderMenu" :style="downMenuStyle">
      <div class="menu">
        <div class="good" @click="handleGoodClick">商品</div>
        <div class="comment">评价</div>
        <div class="detail">详情</div>
      </div>
    </div>
    <div class="icon-up" v-show="showIconUp" :style="upStyle">
        <div class="icon-up-back-wrapper" @click="handleBackClick">
          <div class="iconfont back">&#xe604;</div>
        </div>
        <div class="icon-up-forward-wrapper">
          <div class="iconfont forward">&#xe60b;</div>
        </div>
        <el-dropdown class="icon-up-more-wrapper" trigger="click">
          <div class="iconfont more">&#xe62b;</div>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>消息</el-dropdown-item>
            <el-dropdown-item>首页</el-dropdown-item>
            <el-dropdown-item>搜索</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Header',
  data () {
    return {
      showIconUp: true,
      showHeaderMenu: false,
      upStyle: {
        opacity: 1
      },
      downMenuStyle: {
        opacity: 0
      },
      downIconStyle: {
        opacity: 0
      }
    }
  },
  computed: {
    showIconDown () {
      return !this.showIconUp
    }
  },
  methods: {
    handleScroll () {
      const top = document.documentElement.scrollTop || document.body.scrollTop || window.pageYOffset
      // console.log(top)
      if (top === 0) {
        this.showHeaderMenu = false
        this.showIconUp = true
      } else if (top > 0) {
        this.showHeaderMenu = true
        this.fadeHeaderMenu(top)
        if (top < 100) {
          this.showIconUp = true
          this.fadeIconUp(top)
        } else {
          this.showIconUp = false
          this.fadeIconDown(top)
        }
      }
    },
    fadeHeaderMenu (top) {
      let opacity = top / 200
      if (top >= 200) {
        opacity = 1
      }
      this.downMenuStyle = { opacity }
    },
    fadeIconUp (top) {
      let opacity = 1 - top / 100
      if (opacity <= 0) {
        opacity = 0
      }
      this.upStyle = { opacity }
    },
    fadeIconDown (top) {
      let opacity = (top - 100) / 100
      if (top >= 200) {
        opacity = 1
      }
      this.downIconStyle = { opacity }
    },
    handleGoodClick () {
      console.log('你点击了商品')
    },
    handleBackClick () {
      console.log('返回')
    }
  },
  mounted () {
    window.addEventListener('scroll', this.handleScroll)
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

<style scoped lang="less">
@import '../../assets/styles/varibles.less';
@import '../../assets/styles/mixins.less';
.icon-up {
  z-index: 2;
  display: block;
  position: fixed;
  width: 100%;
  height: .75rem;
  top: 0;
  left: 0;
  right: 0;
  .icon-up-back-wrapper {
    position: absolute;
    left: .1rem;
    top: .1rem;
    .iconUpWrapper();
    .back {
      .iconUpStyle();
    }
  }
  .icon-up-forward-wrapper {
    position: absolute;
    right: .9rem;
    top: .1rem;
    .iconUpWrapper();
    .forward {
      .iconUpStyle();
    }
  }
  .icon-up-more-wrapper {
    position: absolute;
    right: .1rem;
    top: .1rem;
    .iconUpWrapper();
    .more {
      .iconUpStyle();
    }
  }
}
.header-menu {
  z-index: 2;
  display: block;
  position: fixed;
  width: 100%;
  height: .75rem;
  top: 0;
  left: 0;
  right: 0;
  background: #F5F5F5;
  .menu {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    position: absolute;
    top: .1rem;
    width: 50%;
    height: .55rem;
    left: 25%;
    font-size: .3rem;
    .good {
      line-height: .55rem;
    }
    .comment {
      line-height: .55rem;
    }
    .detail {
      line-height: .55rem;
    }
  }
}
.icon-down {
  z-index: 3;
  display: block;
  position: fixed;
  width: 100%;
  height: .75rem;
  top: 0;
  left: 0;
  right: 0;
  .icon-down-back-wrapper {
    position: absolute;
    left: .1rem;
    top: .1rem;
    .iconDownWrapper();
    .back {
      .iconDownStyle();
    }
  }
  .icon-down-forward-wrapper {
    position: absolute;
    right: .9rem;
    top: .1rem;
    .iconDownWrapper();
    .forward {
      .iconDownStyle();
    }
  }
  .icon-down-more-wrapper {
    position: absolute;
    right: .1rem;
    top: .1rem;
    .iconDownWrapper();
    .more {
      .iconDownStyle();
    }
  }
}
</style>



