<template>
    <div class="swiper-wrapper">
        <swiper :options="swiperOption">
            <swiper-slide>
              <img src="http://img12.360buyimg.com/n1/s450x450_jfs/t1/59022/28/10293/141808/5d78088fEf6e7862d/68836f52ffaaad96.jpg"/>
            </swiper-slide>
            <swiper-slide>
              <img src="http://img12.360buyimg.com/n1/s450x450_jfs/t1/59022/28/10293/141808/5d78088fEf6e7862d/68836f52ffaaad96.jpg"/>
            </swiper-slide>
            <swiper-slide>
              <img src="http://img12.360buyimg.com/n1/s450x450_jfs/t1/59022/28/10293/141808/5d78088fEf6e7862d/68836f52ffaaad96.jpg"/>
            </swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
    </div>
</template>

<script>
export default {
  name: 'Carrousel',
  data () {
    return {
      swiperOption: {
        autoplay: false,
        loop: true,
        pagination: '.swiper-pagination'
      }
    }
  }
}
</script>

<style scoped lang="less">
img {
  height: 7rem;
}
</style>

<style>
:root {
    --swiper-theme-color: #696969 ;
}
</style>



